export default function useValidation({ nafn, simanumer, netfang, texti }) {
  const errors = {};

  if (!nafn.length) {
    errors.name = "Nafn nausynlegt";
  }
  if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(netfang)) {
    errors.email = "Email er ekki  rttu snii";
  }

  console.log(/\d{7,20}/.test(simanumer))
  if (!/\d{7,20}/.test(simanumer)) {
    errors.phone = "Smanmer er ekki  rttu snii";
  }
  if (texti.length < 20) {
    errors.text = "Fyrirspurn ekki ngu lng.";
  }

  if (texti.length > 1000) {
    errors.text = "Fyrirspurn alltof lng!";
  }

  return errors;
}
